'use strict';

/**
* Return a list of complex number data types.
*
* @module @stdlib/complex/dtypes
*
* @example
* var dtypes = require( '@stdlib/complex/dtypes' );
*
* var list = dtypes();
* // e.g., returns [ 'complex64', 'complex128' ]
*/

// MODULES //

var dtypes = require( './main.js' );


// EXPORTS //

module.exports = dtypes;
